    --fSecondary1: #13426B;
    --fSecondary2: #9BB8D3;
    --fAccent: #802629;
}

.facilityComponent[rel="youthcare"] {
    --fPrimary1: #004A87;
    --fPrimary2: #8A7A25;
    --fSecondary1: #006093;
    --fSecondary2: #DBD6C1;
    --fAccent: #62A70F;
}

/* Wildcard CTCs */
.facilityComponent[rel*="ctc"] {
    --fPrimary1: #244289;
    --fPrimary2: #436ca9;
    --fSecondary1: #84256c;
    --fSecondary2: #363a35;
    --fAccent: #00aebc;
}

.facilityComponent [class^="CardHeader"] {
    background: var(--fPrimary1) !important;
}

.facilityComponent h3 {
    color: var(--fPrimary2) !important;
}

.facilityComponent h5 {
    color: var(--fPrimary2) !important;
}

.facilityComponent h6 {
    color: var(--fPrimary2) !important;
}

.facilityComponent #monthText {
    color: var(--fPrimary1) !important;
}

.facilityComponent #facilityNameText {
    color: var(--fPrimary1) !important;
}

/* Export Accent color for use in react components */

#admitsGoalTitle {
    color: var(--fPrimary2) !important;
}

.nivoCard [class^="CardBody"] {
    padding: unset !important;
}

.iconText {
    display: inline-block;
}

/* Styles applied for PDF printing */
@media print {
    body {
        -webkit-print-color-adjust: exact !important;
        padding-left: 0px !important;
        text-align: center !important;
    }

    .hidePrint {
        display: none !important;
    }

    .facilityPrint {

    }

    [class^="Card"] {
        display: block !important;
        width: auto !important;
        margin: auto !important;
        border: none !important;
        box-shadow: none !important;
        /*max-height: 600px;*/
    }

    [class^="CardHeader"] {
        width: 100% !important;
        display: block !important;
    }

    #statsSummary {
        margin-top: 40px !important;
    }

    #statsSummary [class^="CardBody"] {
        display: inline-flex !important;
    }

    .facilityPieRow {
        margin-left: -40px !important;
        display: inline-flex !important;
    }

    .nivoCard {
        display: inline-block !important;
        width: 26% !important;
        padding: 4px !important;
        box-shadow: none !important;
        background: none !important;
        margin: unset !important;
        text-align: center !important;
    }

    .nivoCard [class^="CardHeader"] {
        width: 90% !important;
        left: 40px;
    }

    .nivoGraph {
        height: 365px !important;
    }

    .facilityBarChart {
        /*width: 65% !important;*/
        box-shadow: none !important;
        border: none !important;
        background: none !important;
        margin-top: 40px !important;
    }

    .facilityBarChart [class^="CardBody"] {
        padding: 10px 0 !important;
        margin: 30px 0 !important;
        page-break-after: always;
    }

    .chartjs-render-monitor {
        height: unset !important;
        width: 1080px !important;
        display: block;
        margin: auto;
    }

    .historySwitch {
        display: none;
    }

    #facilityLogo {
        max-height: 280px;
    }

    .iconText {
        font-size: 0.8125rem;
        display: block;
    }

    /* Fix for comments label getting cutoff */
    [class^="MuiFormControl"] {
        overflow-x: unset !important;
    }

}
